import * as echarts from '@/components/echarts/echarts.simple.min.js';
import mpvueEcharts from '@/components/echarts/echarts.vue';
import { getMoneyAnalysisByShopId } from '@/request/API/index.js';
var cityList = [
	{
		value: 55,
		name: '北京'
	},
	{
		value: 38,
		name: '上海'
	},
	{
		value: 20,
		name: '广州'
	}
];

let pieOption = {
	animation: false,
	backgroundColor: '#F8F8F8',
	color: ['#37A2DA', '#32C5E9', '#67E0E3', '#91F2DE', '#FFDB5C', '#FF9F7F'],
	series: [
		{
			label: {
				normal: {
					fontSize: 14
				}
			},
			type: 'pie',
			center: ['50%', '50%'],
			radius: [0, '60%'],
			data: [],
			itemStyle: {
				emphasis: {
					shadowBlur: 10,
					shadowOffsetX: 0,
					shadowColor: 'rgba(0, 2, 2, 0.3)'
				}
			}
		}
	]
};
let option = {
	xAxis: {
		type: 'category',
		data: [],
		axisLabel: {
			rotate: 40 //倾斜度
		}
	},
	yAxis: {
		type: 'value',
		splitArea: {
			show: true
		},
		name: '销售金额(元)' // 轴名称
	},
	series: [
		{
			data: [],
			type: 'bar',
			barWidth: 30
		}
	]
};
let lineOption = {
	animation: false,
	color: ['#37A2DA', '#9FE6B8'],
	grid: {
		x: 35,
		x2: 10,
		y: 30,
		y2: 25
	},
	calculable: false,
	xAxis: [
		{
			type: 'category',
			data: []
		}
	],
	yAxis: [
		{
			type: 'value',
			splitArea: {
				show: true
			},
			name: '销售金额(元)' // 轴名称
		}
	],
	series: [
		{
			name: '蒸发量',
			type: 'line',
			data: []
		}
	]
};
export default {
	data() {
		return {
			version: '', //版本号
			tab: 1, //默认柱状图
			dateType: 0, //统计类型 0-日报表/1-月报表/2-季报表/3-年报表(默认传0)
			startTime: '', //开始时间
			endTime: '', //结束时间
			currentTabs: 0,
			echarts: echarts
		};
	},
	components: {
		mpvueEcharts
	},
	computed: {
		...mapState(['userId', 'shopId'])
	},
	onLoad() {
		const userId = uni.getStorageSync('userId');
		const token = uni.getStorageSync('token');
		const shopId = uni.getStorageSync('shopId');
		if (userId) {
			this.$store.commit('SET_SHOPID', shopId);
			this.$store.commit('SET_USERID', userId);
			this.$store.commit('SET_TOKEN', token);
		} else {
			uni.reLaunch({
				url: '/pages/logn/logn'
			});
		}
		this.startTime = this.getNowFormatDate();
		if (this.tab == 1) {
			pieOption.series[0].data = [];
			lineOption.xAxis[0].data = [];
			lineOption.series[0].data = [];
			option.xAxis.data = [];
			option.series[0].data = [];
			this.getMoneyAnalysisByShopId(this.shopId, this.dateType, this.startTime, this.endTime);
		}
		//检查版本更新
		this.version = plus.runtime.version;
		uni.getSystemInfo({
			success: res => {
				console.log(res.platform);
				//检测当前平台，如果是安卓则启动安卓更新
				if (res.platform == 'android') {
					this.AndroidCheckUpdate();
				}
			}
		});
	},
	methods: {
		//检查版本更新
		AndroidCheckUpdate() {
			uni.request({
				url: 'http://114.115.211.170:8018/system/version/getNewVersionNumber?type=2',
				method: 'GET',
				data: {},
				success: res => {
					console.log(res.data.data);
					console.log(this.version);
					if (res.data.data > this.version) {
						if (plus.networkinfo.getCurrentType() != 3) {
							uni.showToast({
								title: '有新的版本发布，检测到您目前非Wifi连接，为节约您的流量，程序已停止自动更新，将在您连接WIFI之后重新检测更新。',
								mask: false,
								duration: 7000,
								icon: 'none'
							});
							return;
						}
						uni.showToast({
							title: '有新的版本发布，检测到您目前为Wifi连接，程序已启动自动更新。新版本下载完成后将自动弹出安装程序。',
							mask: false,
							duration: 7000,
							icon: 'none'
						});
						var dtask = plus.downloader.createDownload('http://114.115.211.170:8008/app/shanghu.apk', {}, function(d, status) {
							// 下载完成
							if (status == 200) {
								plus.runtime.install(plus.io.convertLocalFileSystemURL(d.filename), {}, {}, function(error) {
									uni.showToast({
										title: '安装失败',
										mask: false,
										icon: 'none',
										duration: 1500
									});
								});
							} else {
								uni.showToast({
									title: '更新失败',
									mask: false,
									icon: 'none',
									duration: 1500
								});
							}
						});
						dtask.start();
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		//折线
		zhexian() {
			this.tab = 3;
			pieOption.series[0].data = [];
			lineOption.xAxis[0].data = [];
			lineOption.series[0].data = [];
			option.xAxis.data = [];
			option.series[0].data = [];
			getMoneyAnalysisByShopId(this.shopId, this.dateType, this.startTime, this.endTime).then(res => {
				if (res.data.code == 0) {
					for (let item of res.data.data) {
						let params = item.dateTime;
						let params2 = item.saleMoney;
						lineOption.xAxis[0].data.push(params);
						lineOption.series[0].data.push(params2);
						this.$refs.lineChart.init();
					}
				}
			});
		},
		//饼
		bing() {
			this.tab = 2;
			pieOption.series[0].data = [];
			lineOption.xAxis[0].data = [];
			lineOption.series[0].data = [];
			option.xAxis.data = [];
			option.series[0].data = [];
			getMoneyAnalysisByShopId(this.shopId, this.dateType, this.startTime, this.endTime).then(res => {
				if (res.data.code == 0) {
					for (let item of res.data.data) {
						let params = {
							name: item.dateTime,
							value: item.saleMoney
						};
						pieOption.series[0].data.push(params);
						this.$refs.pieChart.init();
					}
				}
			});
		},
		//柱状图数据
		zhutu() {
			this.tab = 1;
			pieOption.series[0].data = [];
			lineOption.xAxis[0].data = [];
			lineOption.series[0].data = [];
			option.xAxis.data = [];
			option.series[0].data = [];
			getMoneyAnalysisByShopId(this.shopId, this.dateType, this.startTime, this.endTime).then(res => {
				if (res.data.code == 0) {
					for (let item of res.data.data) {
						let params = item.dateTime;
						let params2 = item.saleMoney;
						option.xAxis.data.push(params);
						option.series[0].data.push(params2);
						this.$refs.barChart.init();
					}
				}
			});
		},
		//默认进来获取柱状图报表
		getMoneyAnalysisByShopId(shopId, dateType, startTime, endTime) {
			getMoneyAnalysisByShopId(shopId, dateType, startTime, endTime).then(res => {
				if (res.data.code == 0) {
					for (let item of res.data.data) {
						let params = item.dateTime;
						let params2 = item.saleMoney;
						option.xAxis.data.push(params);
						option.series[0].data.push(params2);
						this.$refs.barChart.init();
					}
				}
			});
		},
		bindPickerChange(res) {
			this.startTime = res.detail.value;
			this.tab = 1;
			pieOption.series[0].data = [];
			lineOption.xAxis[0].data = [];
			lineOption.series[0].data = [];
			option.xAxis.data = [];
			option.series[0].data = [0];
			getMoneyAnalysisByShopId(this.shopId, this.dateType, this.startTime, this.endTime).then(res => {
				if (res.data.code == 0) {
					for (let item of res.data.data) {
						let params = item.dateTime;
						let params2 = item.saleMoney;
						option.xAxis.data.push(params);
						option.series[0].data.push(params2);
						this.$refs.barChart.init();
					}
				}
			});
		},
		bindPickerChange2(res) {
			this.endTime = res.detail.value;
			this.tab = 1;
			pieOption.series[0].data = [];
			lineOption.xAxis[0].data = [];
			lineOption.series[0].data = [];
			option.xAxis.data = [];
			option.series[0].data = [0];
			getMoneyAnalysisByShopId(this.shopId, this.dateType, this.startTime, this.endTime).then(res => {
				if (res.data.code == 0) {
					for (let item of res.data.data) {
						let params = item.dateTime;
						let params2 = item.saleMoney;
						option.xAxis.data.push(params);
						option.series[0].data.push(params2);
						this.$refs.barChart.init();
					}
				}
			});
		},
		//获取当前时间，格式YYYY-MM-DD
		getNowFormatDate() {
			var date = new Date();
			var seperator1 = '-';
			var year = date.getFullYear();
			var month = date.getMonth() + 1;
			var strDate = date.getDate();
			if (month >= 1 && month <= 9) {
				month = '0' + month;
			}
			if (strDate >= 0 && strDate <= 9) {
				strDate = '0' + strDate;
			}
			var currentdate = year + seperator1 + month + seperator1 + strDate;
			return currentdate;
		},
		//饼图
		pieInit(canvas, width, height) {
			let pieChart = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			canvas.setChart(pieChart);

			pieChart.setOption(pieOption);
			return pieChart;
		},
		//折线
		lineInit(canvas, width, height) {
			let lineChart = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			canvas.setChart(lineChart);

			lineChart.setOption(lineOption);
			return lineChart;
		},
		//柱状图
		barInit(canvas, width, height) {
			let barChart = echarts.init(canvas, null, {
				width: width,
				height: height
			});
			canvas.setChart(barChart);

			barChart.setOption(option);
			return barChart;
		},
		tabs(i) {
			this.currentTabs = i;
			this.tab = 1;
			this.dateType = i;
			pieOption.series[0].data = [];
			lineOption.xAxis[0].data = [];
			lineOption.series[0].data = [];
			option.xAxis.data = [];
			option.series[0].data = [];
			getMoneyAnalysisByShopId(this.shopId, this.dateType, this.startTime, this.endTime).then(res => {
				if (res.data.code == 0) {
					for (let item of res.data.data) {
						let params = item.dateTime;
						let params2 = item.saleMoney;
						option.xAxis.data.push(params);
						option.series[0].data.push(params2);
						this.$refs.barChart.init();
					}
				}
			});
		}
	}
};
